<template>
    <v-row class="mt-n14">
        <v-col cols="12" xs="12" sm="6" md="4" lg="3" v-for="product in products" :key="product.id">

            <v-card class="mx-auto my-12 rounded-xl" max-width="374" color="#151515">

                <v-img class="ImagemProduto" @click="productLink(product.id)" height="170"
                    :src="product.attributes.photos.gg[0]"></v-img>

                <v-toolbar color="transparent" class="mt-n7" flat>
                    <v-avatar color="white" rounded class="mr-2">
                        <v-img src="https://staticm.kabum.com.br/public/favicon.ico" contain></v-img>
                    </v-avatar>

                </v-toolbar>
                <v-card-title style="overflow-y: auto; height:150px"
                    class="grey--text text-grey-darken-1 caption mt-n6">{{ product.attributes.title }}
                </v-card-title>
                <v-card-title class="white--text caption justify-center"> R$
                    {{ formatPrice(product.attributes.price_with_discount) }}
                </v-card-title>
                <v-btn block color="grey" elevation="12">Monitorar</v-btn>

            </v-card>

        </v-col>
    </v-row>
</template>

<script>
import axios from "axios";
export default {
    mame: 'Products',
    data() {
        return {
            products: []
        }
    },
    mounted() {
        this.getProducts();
    },
    methods: {
        async getProducts() {

            let config = {
                headers: {
                    'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/101.0.4951.67 Safari/537.36'
                }
            }

            let data = {
                'HTTP_CONTENT_LANGUAGE': self.language
            }
            const response = await axios.get('http://localhost:3000/data', data, config);
            this.products = response.data;
        },
        productLink(id) {
            window.open("https://www.kabum.com.br/produto/" + id, '_blank').focus();
        },
        formatPrice(price) {
            const val = Number(price.toString().replace(",", "."));
            if (!val) return '0,00';
            const valueString = val.toFixed(2).replace(".", ",");
            return valueString.replace(/\B(?=(\d{3})+(?!\d))/g, ".");

        }
    }
}
</script>

<style scoped>
.white--text {
    font-size: 20px !important;
    text-align: center !important;

}


.ImagemProduto {
    cursor: pointer;
}
</style>

